import React, { Component } from 'react';

class LoginForm extends Component{
  state={
    username: '',
    password: '',
    button: 'Login',
    error: ''
  }

  handleUsername = (e) => {
    this.setState({
      username: e.target.value
    })
  }

  handlePassword = (e) => {
    this.setState({
      password: e.target.value
    })
    console.log(this.state.password)
  }

  handleSubmit = (e) => {
    e.preventDefault()

    const email = RegExp('^[_A-Za-z0-9-\\+]+(\\.[_A-Za-z0-9-]+)*@[A-Za-z0-9-]+(\\.[A-Za-z0-9]+)*(\\.[A-Za-z]{2,})$')
    const inputUsername = this.state.username;

    const password = RegExp('[0-9a-zA-Z]{8,}')
    const inputPassword = this.state.password;

    const btn_submit = document.querySelector('.btn_submit')
    
    if(this.state.username.length < 1 || this.state.password.length < 1){
      this.setState({
        error: 'Enter Username and Password'
      })
    } else if(!email.test(inputUsername)){
      this.setState({
        error:'Please provide correct username'
      })
    } else if(!password.test(inputPassword)){
        this.setState({
          error: 'Password should contain atleast eight character'
        })
    } else {
      this.setState({
        error: ''
      })
    }

    if(this.state.username.length < 1 || this.state.password.length < 1 || !email.test(inputUsername) || !password.test(inputPassword)){
    btn_submit.classList.add('btn_submit_error')
    this.setState({
      button: 'Try Again'
    })
    }else{
      btn_submit.classList.remove('btn_submit_error')
      btn_submit.classList.add('btn_submit_success')
      this.setState({
        button: 'Logged in'
      })
    }
  }

  render(){
    return(
      <form onSubmit={this.handleSubmit} className='wrapper'>
          <div className='logo'>
            <h2 className='title'>The <br />
             <span className="drinks">Drinks</span>
            </h2>
          </div>
          <div className='user_detail'>
            <div className='user_username'>
              <label htmlFor='username'>Username</label>
              <input type='text' id='username' className='input username' onChange={this.handleUsername}/>
              
            </div>
            <div className='user_password'>
              <label htmlFor='password'>Password</label>
              <input type='password' id='password' className='input password' onChange={this.handlePassword}/>
            <p className='error'>{this.state.error}</p>
            </div>
          </div>
          <button id='btn' className='btn_submit'>{this.state.button}</button>
      </form>
    )
  }
}

export default LoginForm;